import React, { useState } from 'react';
import './App.css';
import { Clicker } from './Clicker/Clicker';
import { Score } from './Score/Score';

function App() {
	const [score, setScore] = useState(0);

	const handleClick = () => {
		setScore(score => score + 1);
	}

	return (
		<div className="App">
			<h1>Title</h1>
			<Clicker onClick={handleClick}/>
			<Score score={score}/>
		</div>
	);
}

export default App;
