import './Clicker.scss'
import image from '../assets/coin.png'
import { useState } from 'react';

interface ClickerProps {
	onClick: () => void
}

export const Clicker = ({ onClick }: ClickerProps) => {
	const [animate, setAnimate] = useState(false);
	const [client, setClient] = useState({ clientX: 0, clientY: 0 })


	const handleClick = (event: any) => {
		setAnimate(true);
		setClient({ clientX: event.clientX, clientY: event.clientY });
		onClick()
		setTimeout(() => setAnimate(false), 200);
	};

	return (
		<div className={'clickerComponent'}>
			<img src={image} alt={''} className={animate ? 'pulse' : 'clicker'} onClick={handleClick}/>
			<Popup animate={animate} clientX={client.clientX} clientY={client.clientY}/>
		</div>
	)
}

interface PopupProps {
	animate: boolean;
	clientX: number,
	clientY: number
}

const Popup = ({ animate, clientX, clientY }: PopupProps) => {

	if (animate) {
		return (
			<div style={{ position: 'absolute', left: clientX, top: clientY - 30 }}>+1</div>
		)
	}
	return null

}
